
HTML, BODY {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}
H1 {
  margin: 4px;
}
#app {
  font-family: Avenir, Nunito, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin: 0;
  padding: 0;
  height: 100%;
}
.right-pane .text, .inline .text {
  margin-top: 20px;
  border: none;
  background-color: transparent;
  resize: none;
  outline: none;
  font-size: 16px;
  height: 100%;
  white-space: pre-line;
  width: 100%;
}
.directive {
  color: #990000;
}
.md {
  margin-left: 10px;
  margin-top: 10px;
  max-width: 700px;
  padding-right: 10px;
}
.unselectable {
  -moz-user-select: -moz-none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
a {
  color: #42b983;
}



