
.topItem SPAN {
  font-size: 30px;
  line-height: 1.5;
}
.topItem {
  margin-bottom: 10px;
}
.topItemIcon {
  vertical-align: top;
  padding-left: 20px;
  margin-right: -10px;
}
.icon-bracket {
  display:inline-block;
  height: 100%;
  vertical-align: middle;
  padding-bottom: 8px;
  padding-left: 0;
}
.icon-bracket .arrow {
  display:inline-block;
  height: 100%;
  vertical-align: middle;
  padding-bottom: 2px;
  width: 20px;
}
.arrow {
  -webkit-transition: all .1s ease;
  transition: all .1s ease;
  text-align: center;
  display: inline-block;
  width: 11px;
  height: 21px;
  margin-top: 2px;
}
.arrowdown {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}
.item {
  cursor: pointer;
  width: 100%;
}
.bold {
  font-weight: bold;
}
ul {
  padding-left: 1em;
  line-height: 1.4em;
  list-style-type: none;
  margin-bottom: 8px;
}
li {
  white-space: nowrap;
  min-width: 760px;
  margin-bottom: 4px;
  margin-top: 4px;
}
li > div {
  padding-left:4px;
}
.active {
  background: #81ff00;
  max-width: 762px;
}
.activeb {
  background: #81ff00;
  font-weight:bold;
}
.inline .content {
  max-width: 700px;
  white-space: normal;
  padding:30px;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-top: 4px;
  margin-bottom: 16px;
  border:1px solid #ccc;
  border-radius: 4px;
  // box-shadow: -4px 0 8px -4px rgba(31, 31, 31, 0.8)
}
.vinline {
  max-width: 700px;
  white-space: normal;
  padding:0;
  padding-top: 0;
  padding-bottom: 0;
  margin-top: 4px;
  margin-bottom: 16px;
  height: 600px;
  width: 760px;
  border:1px solid #ccc;
  border-radius: 4px;
  overflow: auto;
// box-shadow: -4px 0 8px -4px rgba(31, 31, 31, 0.8)
}
.hshim {
  height: 15px;
}
.otitle {
  padding-left: 4px;
}
.child-items {
  margin: 0;
  padding: 0;
}



